@import '../../static/scss/variables';


.live{
	height: 100vh;
	background-color: $color_black;
	padding-top: 70px;
	padding-bottom: 70px;
	display: flex;
	flex-direction: column;

	.container{
		flex: 1 1 auto;
	}

	&__wrapper{
		width: 100%;
		height: 100%;
	}
	&__stream{
		border-radius: 30px;
		width: 100%;
		height: 100%;
	}
}