@import '../../static/scss/variables';


.team{
	&__img{
		border-radius: 50%;
		border: 5px solid $color_orange;
		position: relative;

		img{
			width: 100%;
			height: 100%;
			object-fit: cover;
			position: absolute;
			top: 0;
			left: 0;
			border-radius: 50%;
			padding: 10px;
		}
	}
}
